body {
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
}

header {
  color: #fff;
  text-align: center;
  background-color: #240fc2;
  padding: 20px 0;
}

.search-form {
  text-align: center;
}

input[type="text"] {
  width: 60%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: #555;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.photo-card {
  width: calc(25% - 10px);
  box-sizing: border-box;
  margin-bottom: 20px;
}

.photo-card img {
  max-width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  background-color: #fff;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.photo-card .info-item {
  margin: 0;
  padding: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  transition: background-color .3s;
  display: none;
}

.load-more:hover {
  background-color: #555;
}

